import * as Yup from 'yup'
import { phoneNumber } from './constants/stringPatterns'
import { select } from '../../../data/forms/teacherFields.json'
import { fieldChoices } from '../../../utils/formsUtils'

const choices = fieldChoices(select)

export default function teacherFormValidationSchema(specialtyListData) {
	return Yup.object().shape({
		name: Yup.string()
			.min(2, '  2 .')
			.max(128, ' 128 .')
			.required(' ,     ().'),
		specialties: Yup.array().of(
			Yup.string()
				.oneOf(specialtyListData, '      .')
				.required('   \'.')
		),
		dateOfBirth: Yup.date()
			.min(new Date(1940, 0, 1), ' .')
			.max(new Date(2010, 0, 1), ' .')
			.required('  .'),
		employmentDate: Yup.date()
			.min(new Date(1940, 0, 1), ' .')
			.max(new Date(2040, 0, 1), ' ?')
			.required(' .'),
		years: Yup.number()
			.typeError('  .')
			.min(0, '  .')
			.max(99, ' ?'),
		months: Yup.number()
			.typeError('  .')
			.min(0, '  .')
			.max(11, '.'),
		days: Yup.number()
			.typeError('  .')
			.min(0, '  .')
			.max(31, '.'),
		weekWorkHours: Yup.number()
			.typeError('  .')
			.min(0, '  .')
			.max(100, '.'),
		phone: Yup.string()
			.length(19, ' , 19 : +38 (XXX) XXX-XX-XX')
			.matches(phoneNumber, ' ,  : +38 (XXX) XXX-XX-XX')
			.required('  .'),
		contactEmail: Yup.string().trim()
			.email('   .')
			.required('  .'),
		residence: Yup.string()
			.oneOf(choices.residence, '  .')
			.required(' .'),
		gender: Yup.string()
			.oneOf(choices.gender, ' .')
			.required(' .'),
		maritalStatus: Yup.string()
			.oneOf(choices.maritalStatus, ' .')
			.required('  .'),
		university: Yup.string()
			.min(5, '  5 .')
			.max(128, ' 128 .')
			.required('   .'),
		educationType: Yup.string()
			.oneOf(choices.educationType, ' .')
			.required('  .'),
		educationDegree: Yup.string()
			.oneOf(choices.educationDegree, ' .')
			.required(' - .'),
		qualification: Yup.string()
			.oneOf(choices.qualification, ' .')
			.required('  .'),
		teacherTitle: Yup.string()
			.oneOf(choices.teacherTitle, ' .')
			.required('  .'),
		scienceDegree: Yup.string()
			.oneOf(choices.scienceDegree, ' .')
			.required('  .'),
		category: Yup.string()
			.oneOf(choices.category, ' .')
			.required(' .'),
		employeeType: Yup.string()
			.oneOf(choices.employeeType, ' .')
			.required('  .'),
		isAdministration: Yup.bool()
			.oneOf([true, false]),
		isRetired: Yup.bool()
			.oneOf([true, false]),
		employeeIsAStudent: Yup.bool()
			.oneOf([true, false]),
		accomplishmentsDscr: Yup.string()
			.min(5, '  5 .')
			.max(2500, ' 2500 .'),
		info: Yup.string()
			.min(3, '  3 .')
			.max(255, ' 255 .')
	})
}
