import * as Yup from 'yup'

export default function contactFormValidationSchema() {
	return Yup.object().shape({
		name: Yup.string().trim()
			.min(2, ' 2 .')
			.max(30, ' 30 .')
			.required('  \'.'),
		email: Yup.string().trim()
			.email('   .')
			.required('   .'),
		message: Yup.string().trim()
			.min(8, ' 8 .')
			.max(280, ' 280 .')
			.required(' ,   .')
	})
}
