import * as Yup from 'yup'
import { mediumStrPass } from './constants/stringPatterns'

export default function loginFormValidationSchema() {
	return Yup.object().shape({
		email: Yup.string().trim()
			.email('   .')
			.required('   .'),
		password: Yup.string()
			.min(8, ' 8 .')
			.matches(mediumStrPass,
				' 8 ,    ,      .')
			.required(' ,   .')
	})
}
